import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest }) => ({
  /**
   * @name Intents#selectDebitAccount
   * @description The intent to select a debit account.
   * @type {function}
   */
  selectDebitAccount: createRequest(Intent.SELECT_DEBIT_ACCOUNT),

  /**
   * @name Intents#showDebitAccounts
   * @description Handles the intent to show a list of debit accounts.
   * @type {function}
   */
  showDebitAccounts: handleRequest(Intent.SHOW_DEBIT_ACCOUNTS),
});



